import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import HeadPindahBarangSatuBaki from "./HeadPindahBarangSatuBaki.jsx";
import Swal from "sweetalert2";
import { setBarangPindahSatuBaki } from "../../../actions/databarang_action.jsx";
import { postData } from "../../../config/axios.jsx";
import { NotifError, ToastNotification } from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
const maptostate = (state) => {
  return {
    setBarangPindahSatuBaki: state.databarang.setBarangPindahSatuBaki,
  };
};

class FormPindahBarangSatuBaki extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footerAttrs: {
            colSpan: "5",
          },
          footer: (cell, row, num, index) => {
            return (
              <div>Total Barang : {this.props.setBarangPindahSatuBaki.length === 0
              ? JSON.parse(localStorage.getItem("data_barang_satu_baki_tmp")) === null  ? 0 : JSON.parse(localStorage.getItem("data_barang_satu_baki_tmp")).length 
              : this.props.setBarangPindahSatuBaki.length} </div>
            );
          },
        },
        {
          dataField: "kode_group",
          text: "Gudang Asal",
        },
        {
          dataField: "kode_dept",
          text: "Baki Asal",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "nama_atribut",
          text: "Nama Atribut",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return cell.toFixed(1);
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0).toFixed(1),
        },

      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let data = JSON.parse(localStorage.getItem('data_barang_satu_baki_tmp'));
        data.splice(id, 1)
        localStorage.setItem('data_barang_satu_baki_tmp', JSON.stringify(data));
        this.props.dispatch(setBarangPindahSatuBaki(data));
        // deleteLocalItemBarcode("data_barang_satu_baki_tmp", id);
        // this.props.dispatch(getListReturnSupplier());
      }
    });
  }
  componentDidMount() {
    // this.props.dispatch(getDataBaki());
    // localStorageencryp('data_barang',[])
    // localStorage.setItem('data_barang_satu_baki_tmp',[])
      
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    // let barang_tmp = this.props.setBarangPindahSatuBaki.length === 0 ? JSON.parse(localStorage.getItem('data_barang_satu_baki_tmp')) : this.props.setBarangPindahSatuBaki 
    let hasil = {
      kode_gudang: data.gudang_tujuan,
      kode_toko: data.baki_tujuan,
      kode_gudang_asal: data.gudang_asal,
      kode_toko_asal: data.baki_asal
      }
    //   console.log(hasil);
    postData('pindahbarang/addbanyak', hasil)
    .then(() => {
      this.setState({ isLoading: false });
    }).then(() => {
      localStorage.setItem('data_barang_satu_baki_tmp', JSON.stringify([]));
    })
    .then(() => {
      this.props.dispatch(setBarangPindahSatuBaki([]));
    })
    .then(() => {
      this.props.dispatch(reset('HeadPindahBarangSatuBaki'))
    })
    .then(() => {
      ToastNotification("success", "Data Berhasil Disimpan");
    }).catch((error) => this.errorhandling(error))
  }
  errorhandling(error) {
    NotifError(error.response.data)
    this.setState({ isLoading: false });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Barang</Link>
          </li>
          <li className="breadcrumb-item active">Pindah Barang Satu Baki</li>
        </ol>
        <h1 className="page-header">Pindah Barang Satu Baki </h1>
        <Panel>
          <PanelHeader>Pindah Barang Satu Baki</PanelHeader>
          <br />

          {/* Pindah Barang Satu Baki */}
          <div className="container">
            <HeadPindahBarangSatuBaki
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
              data={this.props.setBarangPindahSatuBaki}
              columns={this.state.columns}
            />
          </div>
          <br />
          {/* End Pindah Barang Satu Baki */}

          {/* End Tambah Pindah Barang Satu Baki  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormPindahBarangSatuBaki);
