import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { setBarangPindahSatuBaki } from "../../../actions/databarang_action";
import { getDataBaki, getDataGudang } from "../../../actions/datamaster_action";
import {
  ReanderSelect,
  SkeletonLoading,
} from "../../../components/helpers/field";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { getData } from "../../../config/axios";
import ValidasiPindahBarangBaki from "../../../Validasi/Barang/PindahBarangSatuBaki/ValidasiPindahBarangBaki";
const maptostate = (state) => {
  return {
    datagudang: state.datamaster.getDataGudang,
    databaki: state.datamaster.getDataBaki,
    setBarangPindahSatuBaki: state.databarang.setBarangPindahSatuBaki,
  };
};
class HeadPindahBarangSatuBaki extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      isLoadingBaki: false,
      isLoadingBatal: false,
      bakigudangtujuan: false,
      loadingpencarian: false,
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataGudang());
    this.props.dispatch(getDataBaki());
    setTimeout(() => {
      // document.getElementById('kode_gudang').p
      this.setState({ gudang_asal: 'TOKO' });
      this.props.change('gudang_asal','TOKO')
    }, 100);
  }

  filterLaporan() {
    this.setState({ loadingpencarian: true });
    this.setState({ bakigudangtujuan: true });
    getData("barang/get/by-kode-baki/" + this.state.baki_asal)
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({ loadingpencarian: false, bakigudangtujuan: false });
          ToastNotification("info", "Data Yang Dicari Tidak Ada");
          localStorage.setItem("data_barang_satu_baki_tmp", JSON.stringify([]));
          this.props.dispatch(setBarangPindahSatuBaki([]));
          // return false
        } else {
          localStorage.setItem("data_barang_satu_baki_tmp", JSON.stringify([]));
          localStorage.setItem(
            "data_barang_satu_baki_tmp",
            JSON.stringify(res.data)
          );
          this.props.dispatch(setBarangPindahSatuBaki(res.data));
          this.setState({ loadingpencarian: false });
        }
      })
      .catch((err) => {
        this.setState({ bakigudangtujuan: false });
        this.errorHandling(err);
      });
  }

  errorHandling(err) {
    this.setState({ loadingpencarian: false, bakigudangtujuan: false });
    ToastNotification("info", "Data Yang Dicari Tidak Ada");
    localStorage.setItem("data_barang_satu_baki_tmp", JSON.stringify([]));
    this.props.dispatch(setBarangPindahSatuBaki([]));
  }
  batalHancur() {
    this.setState({ isLoadingBatal: true })
    setTimeout(() => {
      this.props.dispatch(setBarangPindahSatuBaki([]));
      localStorage.setItem('data_barang_satu_baki_tmp', JSON.stringify([]))
      this.setState({ isLoadingBatal: false })
    }, 500);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off"
      onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}
      >
        <div className="row">
          <div className="col-lg-4">
            <Field
              label="Gudang Asal"
              name="gudang_asal"
              onChange={(e) => {
                this.setState({
                  gudang_asal: e,
                  bakigudangtujuan: false,
                });
                this.props.change("baki_asal", "");
                this.props.dispatch(setBarangPindahSatuBaki([]));
                localStorage.setItem(
                  "data_barang_satu_baki_tmp",
                  JSON.stringify([])
                );
              }}
              options={this.props.datagudang
                .filter((list) => list.kode_gudang !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_gudang,
                    name: list.kode_gudang + " - " + list.nama_gudang,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Gudang Asal"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4">
            {this.state.isLoadingBaki === false ? (
              <Field
                label="Baki Asal"
                name="baki_asal"
                onChange={(e) => {
                  this.setState({ baki_asal: e });
                }}
                options={this.props.databaki
                  .filter((list) => list.kode_gudang === this.state.gudang_asal)
                  .map((databaki) => {
                    let data = {
                      value: databaki.kode_baki,
                      name: databaki.kode_baki + " - " + databaki.nama_baki,
                    };
                    return data || [];
                  })}
                placeholder={"Silahkan Pilih Baki Asal"}
                component={ReanderSelect}
              />
            ) : (
              <SkeletonLoading label="Baki Asal" />
            )}
          </div>
          <div className="col-lg-4">
            <label> &nbsp; </label>
            <button
              className="btn btn-primary btn-block"
              disabled={this.submitting}
              onClick={() => this.filterLaporan()}
              type="button"
            >
              {this.state.loadingpencarian ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Mencari Data
                </>
              ) : (
                "Cari Data"
              )}
            </button>
          </div>

          {this.state.bakigudangtujuan === false ? null : (
            <>
              <div className="col-lg-6">
                <Field
                  label="Gudang Tujuan"
                  name="gudang_tujuan"
                  onChange={(e) => {
                    this.setState({ gudang_tujuan: e });
                    this.props.change("baki_tujuan", "");
                  }}
                  options={this.props.datagudang
                    .filter((list) => list.kode_gudang !== undefined)
                    .map((list) => {
                      let data = {
                        value: list.kode_gudang,
                        name: list.kode_gudang + " - " + list.nama_gudang,
                      };
                      return data;
                    })}
                  placeholder={"Silahkan Pilih Gudang Tujuan"}
                  component={ReanderSelect}
                />
              </div>
              <div className="col-lg-6">
                {this.state.isLoadingBaki === false ? (
                  <Field
                    label="Baki Tujuan"
                    name="baki_tujuan"
                    options={this.props.databaki
                      .filter(
                        (list) => list.kode_gudang === this.state.gudang_tujuan
                      )
                      .map((databaki) => {
                        let data = {
                          value: databaki.kode_baki,
                          name: databaki.kode_baki + " - " + databaki.nama_baki,
                        };
                        return data || [];
                      })}
                    placeholder={"Silahkan Pilih Baki Tujuan"}
                    component={ReanderSelect}
                  />
                ) : (
                  <SkeletonLoading label="Baki Tujuan" />
                )}
              </div>
              {/* {console.log(this.props.setBarangPindahSatuBaki)}
            {console.log(JSON.parse(localStorage.getItem("data_barang_satu_baki_tmp")))} */}
            </>
          )}
        </div>
        <Tabel
          keyField="kode_barcode"
          exportCsv={false}
          data={
            this.props.setBarangPindahSatuBaki.length === 0
              ? JSON.parse(localStorage.getItem("data_barang_satu_baki_tmp"))
              : this.props.setBarangPindahSatuBaki
          }
          columns={this.props.columns}
          empty={true}
          pagination="off"
          textEmpty="Data Barang Kosong"
        />
        <div className="row">
          <div className="col-10">
            <button
              className="btn btn-primary"
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
            &nbsp;
            <button
              className="btn btn-warning"
              type="button"
              onClick={() => this.batalHancur()}
              disabled={this.props.isLoadingBatal}
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Membatal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
          {/* <div className="2">
            {JSON.parse(localStorage.getItem("data_barang_satu_baki_tmp")) ===
            null ? null : (
              <>
                Total Barang :{" "}
                {this.props.setBarangPindahSatuBaki.length === 0
                  ? JSON.parse(
                      localStorage.getItem("data_barang_satu_baki_tmp")
                    ).length || 0
                  : this.props.setBarangPindahSatuBaki.length || 0}
                <br />
                Total Berat :
                {this.props.setBarangPindahSatuBaki.length === 0
                  ? JSON.parse(
                      localStorage.getItem("data_barang_satu_baki_tmp")
                    )
                      .map((column, key) => column.berat)
                      .reduce((a, b) => a + b, 0)
                      .toFixed(1)
                  : this.props.setBarangPindahSatuBaki
                      .map((column, key) => column.berat)
                      .reduce((a, b) => a + b, 0)
                      .toFixed(1)}
              </>
            )}
          </div> */}
        </div>
      </form>
    );
  }
}

HeadPindahBarangSatuBaki = reduxForm({
  form: "HeadPindahBarangSatuBaki",
  enableReinitialize: true,
  validate: ValidasiPindahBarangBaki,
})(HeadPindahBarangSatuBaki);
export default connect(maptostate, null)(HeadPindahBarangSatuBaki);
