const ValidasiPindahBarangBaki = (value) => {
    const errors = {};
    if (!value.baki_asal) {
        errors.baki_asal = "Baki Asal Tidak Boleh Kosong";
    }
    if (!value.baki_tujuan) {
        errors.baki_tujuan = "Baki Tujuan Tidak Boleh Kosong";
    }
    if (!value.gudang_asal) {
        errors.gudang_asal = "Gudang Asal Tidak Boleh Kosong";
    }
    if (!value.gudang_tujuan) {
        errors.gudang_tujuan = "Gudang Tujuan Tidak Boleh Kosong";
    }

    return errors;
}
export default ValidasiPindahBarangBaki;


